import React from 'react';
import { useState } from 'react';
import useViewport from '.././utils/useViewport';
import {isMobile} from 'react-device-detect';

const downArrow = (
  <svg clip-rule="evenodd" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="2" height="24" width="24" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg" fill="currentColor"><path d="m16.843 10.211c.108-.141.157-.3.157-.456 0-.389-.306-.755-.749-.755h-8.501c-.445 0-.75.367-.75.755 0 .157.05.316.159.457 1.203 1.554 3.252 4.199 4.258 5.498.142.184.36.29.592.29.23 0 .449-.107.591-.291 1.002-1.299 3.044-3.945 4.243-5.498z"/></svg>
)

const Dropdown = (props:any) => {
  var classNames = require('classnames');
  const {head, body} = props;
  const [open, setOpen] = useState(false);

  return (
    <div className="overflow-hidden">
      <button
        onClick={() => setOpen(!open)}
        className={classNames({
          "flex gap-x-2 text-primary items-center w-fit": true,
          "hover:text-secondary": !isMobile})}
      >
        <div className="text-xl font-head font-semibold">
          {head}
        </div>
        <div className={classNames({
          "transform rotate-180":open})}
        >
          {downArrow}
        </div>
      </button>
      <div className={classNames({
        "hidden": open,
        "p-2": true})}
      >
        {body}
      </div>
    </div>
  )
}

export default Dropdown;
